___CSS_LOADER_EXPORT___.push([module.id, `@font-face {
    font-family: 'myfont';
    src: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    font-style: normal;
}
:root {
    --navercolor: rgb(100, 241, 100);
    --layoutcolor: rgba(196, 192, 192, 0.671);
}

html {
    font-family: 'myfont';
    margin: 0;
}

body {
    margin: 0;
    padding: 0;
    display:flex;
    flex-direction: column;
}

@supports (height: 100vh) {
    body {
        background: linear-gradient(to right top, var(--navercolor) 50%, white 50%);
        background-size: 100% calc(100% - 100vh + 128px);
        background-repeat: no-repeat;
    }

    body:before {
        content: '';
        position: fixed;
        top: 128px;
        bottom: 0;
        width: 100%;
        z-index: -1;
        background: white;
    }
}

header {
    border-bottom: 1px solid var(--layoutcolor);
    position: fixed;
    top: 0;
    width: 100%;
    height: 124px;
    background-color: white;
    z-index: 1;
}

h1 {
    font-size: 3rem;
}

header>h2,
h1 {
    margin: 0;
    text-decoration: var(--layoutcolor) underline;
    padding-left: 2rem;
    color: var(--navercolor);
}

.tablist {
    display: flex;
    align-items: center;
    position: absolute;
    bottom: 0;
    width: 100%;
}

header p {
    margin: 0;
    font-size: 1.5rem;
    color: var(--layoutcolor);
}

header p:hover {
    cursor: pointer;
}
.selected{
    background-color: var(--navercolor);
    color:black;
}
.tab{
    padding:10px;
    padding-bottom:0;
    text-align: center;
    flex:1;
    border-top-right-radius: 10px;
    border-top-left-radius: 10px;
    position:relative;
}
main {
    display: grid;
    margin-top: 128px;
    grid-template: 60px 1fr 1fr/1fr auto 1fr;
    flex:1;
}

#content {
    border: 1px solid var(--layoutcolor);
    grid-column: 2/3;
    grid-row: 2/3;
    box-shadow: 2px 2px 1px black;

}

#sidebar {
    border-radius: 1rem;
    border: 1px solid var(--layoutcolor);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    position: fixed;
    right: 0;
    top: 256px;
    background-color: white;
}

#sidebar a {
    border-bottom: 1px solid var(--layoutcolor);
    text-align: center;
    padding: 20px;
}
a:active{
    text-decoration: none;
}
a{
    text-decoration: none;
    color:black;
    text-shadow: 1px 1px 1px var(--navercolor);
}
#sidebar a:nth-last-child(1) {
    border-bottom: none;
}

.sideImage {
    width: 40px;
    border-radius: 5px;
}

footer {
    background-color: black;
    color: white;
    height: 2rem;
    text-align: center;
}

footer p {
    padding: 0;
    margin: 0;
}`, "",{"version":3,"sources":["webpack://./src/styles/style.css"],"names":[],"mappings":"AAAA;IACI,qBAAqB;IACrB,4CAA4C;IAC5C,kBAAkB;AACtB;AACA;IACI,gCAAgC;IAChC,yCAAyC;AAC7C;;AAEA;IACI,qBAAqB;IACrB,SAAS;AACb;;AAEA;IACI,SAAS;IACT,UAAU;IACV,YAAY;IACZ,sBAAsB;AAC1B;;AAEA;IACI;QACI,2EAA2E;QAC3E,gDAAgD;QAChD,4BAA4B;IAChC;;IAEA;QACI,WAAW;QACX,eAAe;QACf,UAAU;QACV,SAAS;QACT,WAAW;QACX,WAAW;QACX,iBAAiB;IACrB;AACJ;;AAEA;IACI,2CAA2C;IAC3C,eAAe;IACf,MAAM;IACN,WAAW;IACX,aAAa;IACb,uBAAuB;IACvB,UAAU;AACd;;AAEA;IACI,eAAe;AACnB;;AAEA;;IAEI,SAAS;IACT,6CAA6C;IAC7C,kBAAkB;IAClB,wBAAwB;AAC5B;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,kBAAkB;IAClB,SAAS;IACT,WAAW;AACf;;AAEA;IACI,SAAS;IACT,iBAAiB;IACjB,yBAAyB;AAC7B;;AAEA;IACI,eAAe;AACnB;AACA;IACI,mCAAmC;IACnC,WAAW;AACf;AACA;IACI,YAAY;IACZ,gBAAgB;IAChB,kBAAkB;IAClB,MAAM;IACN,6BAA6B;IAC7B,4BAA4B;IAC5B,iBAAiB;AACrB;AACA;IACI,aAAa;IACb,iBAAiB;IACjB,wCAAwC;IACxC,MAAM;AACV;;AAEA;IACI,oCAAoC;IACpC,gBAAgB;IAChB,aAAa;IACb,6BAA6B;;AAEjC;;AAEA;IACI,mBAAmB;IACnB,oCAAoC;IACpC,aAAa;IACb,sBAAsB;IACtB,8BAA8B;IAC9B,mBAAmB;IACnB,eAAe;IACf,QAAQ;IACR,UAAU;IACV,uBAAuB;AAC3B;;AAEA;IACI,2CAA2C;IAC3C,kBAAkB;IAClB,aAAa;AACjB;AACA;IACI,qBAAqB;AACzB;AACA;IACI,qBAAqB;IACrB,WAAW;IACX,0CAA0C;AAC9C;AACA;IACI,mBAAmB;AACvB;;AAEA;IACI,WAAW;IACX,kBAAkB;AACtB;;AAEA;IACI,uBAAuB;IACvB,YAAY;IACZ,YAAY;IACZ,kBAAkB;AACtB;;AAEA;IACI,UAAU;IACV,SAAS;AACb","sourcesContent":["@font-face {\r\n    font-family: 'myfont';\r\n    src: url('/src/assets/font/Ubuntu-Bold.ttf');\r\n    font-style: normal;\r\n}\r\n:root {\r\n    --navercolor: rgb(100, 241, 100);\r\n    --layoutcolor: rgba(196, 192, 192, 0.671);\r\n}\r\n\r\nhtml {\r\n    font-family: 'myfont';\r\n    margin: 0;\r\n}\r\n\r\nbody {\r\n    margin: 0;\r\n    padding: 0;\r\n    display:flex;\r\n    flex-direction: column;\r\n}\r\n\r\n@supports (height: 100vh) {\r\n    body {\r\n        background: linear-gradient(to right top, var(--navercolor) 50%, white 50%);\r\n        background-size: 100% calc(100% - 100vh + 128px);\r\n        background-r