___CSS_LOADER_EXPORT___.push([module.id, `:root {
    --navercolor: rgb(100, 241, 100);
    --layoutcolor: rgba(196, 192, 192, 0.671);
}

.loadingbackground{
    display:flex;
    position:fixed;
    bottom:0;
    top:0;
    left:0;
    right:0;
    background-image: linear-gradient(to right,var(--layoutcolor),var(--navercolor));
    background-size: 300%;
    animation: 2s linear infinite load;
    justify-content: center;
    z-index: 1;
}
.loadingbody{
    width: 50vw;
    height:50vh;
    align-self: center;
    background-image: linear-gradient(to right,var(--layoutcolor),var(--navercolor));
    animation : 2s linear infinite load;
    background-size: 300%;
    display:flex;
    justify-content: center;
    align-items: center;
    font-size: 3rem;
    color:white;
}
@keyframes load {
    100%{
        background-position: right;
    }
    0%{
        background-position: center;
    }
}`, "",{"version":3,"sources":["webpack://./src/styles/loading.css"],"names":[],"mappings":"AAAA;IACI,gCAAgC;IAChC,yCAAyC;AAC7C;;AAEA;IACI,YAAY;IACZ,cAAc;IACd,QAAQ;IACR,KAAK;IACL,MAAM;IACN,OAAO;IACP,gFAAgF;IAChF,qBAAqB;IACrB,kCAAkC;IAClC,uBAAuB;IACvB,UAAU;AACd;AACA;IACI,WAAW;IACX,WAAW;IACX,kBAAkB;IAClB,gFAAgF;IAChF,mCAAmC;IACnC,qBAAqB;IACrB,YAAY;IACZ,uBAAuB;IACvB,mBAAmB;IACnB,eAAe;IACf,WAAW;AACf;AACA;IACI;QACI,0BAA0B;IAC9B;IACA;QACI,2BAA2B;IAC/B;AACJ","sourcesContent":[":root {\r\n    --navercolor: rgb(100, 241, 100);\r\n    --layoutcolor: rgba(196, 192, 192, 0.671);\r\n}\r\n\r\n.loadingbackground{\r\n    display:flex;\r\n    position:fixed;\r\n    bottom:0;\r\n    top:0;\r\n    left:0;\r\n    right:0;\r\n    background-image: linear-gradient(to right,var(--layoutcolor),var(--navercolor));\r\n    background-size: 300%;\r\n    animation: 2s linear infinite load;\r\n    justify-content: center;\r\n    z-index: 1;\r\n}\r\n.loadingbody{\r\n    width: 50vw;\r\n    height:50vh;\r\n    align-self: center;\r\n    background-image: linear-gradient(to right,var(--layoutcolor),var(--navercolor));\r\n    animation : 2s linear infinite load;\r\n    background-size: 300%;\r\n    display:flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n    font-size: 3rem;\r\n    color:white;\r\n}\r\n@keyframes load {\r\n    100%{\r\n        background-position: right;\r\n    }\r\n    0%{\r\n        background-position: center;\r\n    }\r\n}"],"sourceRoot":""}]);
